<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          
          <a-col :sm="6">
            <!-- 参数卡名称 -->
            <a-form-model-item
              label="参数卡名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入参数卡名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 参数卡类型 -->
            <a-form-model-item
              label="参数卡类型"
              prop="levelType"
              ref="levelType"
            >
              <a-select
                v-model="entity.levelType"
                placeholder="请选择参数卡类型"
                name="levelType"
              >
                <a-select-option value="质量参数卡">质量参数卡</a-select-option>
                <a-select-option value="工艺参数卡">工艺参数卡</a-select-option>
                <a-select-option value="性能参数卡">性能参数卡</a-select-option>
                <a-select-option value="安全参数卡">安全参数卡</a-select-option>
                <a-select-option value="环保参数卡">环保参数卡</a-select-option>
                <a-select-option value="其他参数卡">其他参数卡</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status" name="status">
                <a-radio value="启用">启用</a-radio>
                <a-radio value="禁用">禁用</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 医疗器械注册证号 -->
            <a-form-model-item
              label="医疗器械注册证号"
              prop="registrationNo"
              ref="registrationNo"
            >
              <a-input
                v-model="entity.registrationNo"
                placeholder="请输入医疗器械注册证号"
                name="registrationNo"
              ></a-input>
              </a-form-model-item>
            </a-col>
          <a-col :sm="6">
            <!-- 生产许可证号 -->
            <a-form-model-item
              label="生产许可证号"
              prop="productionLicenseNo"
              ref="productionLicenseNo"
            >
              <a-input
                v-model="entity.productionLicenseNo"
                placeholder="请输入生产许可证号"
                name="productionLicenseNo"
              ></a-input>
              </a-form-model-item>
            </a-col>
          <a-col :sm="6">
            <!-- 风险等级 -->
            <a-form-model-item
              label="风险等级"
              prop="riskLevel"
              ref="riskLevel"
            >
              <a-select
                v-model="entity.riskLevel"
                placeholder="请选择风险等级"
                name="riskLevel"
              >
                <a-select-option value="高风险">高风险</a-select-option>
                <a-select-option value="中风险">中风险</a-select-option>
                <a-select-option value="低风险">低风险</a-select-option>
              </a-select>
              </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 验证要求 -->
            <a-form-model-item
              label="验证要求"
              prop="validationRequirements"
              ref="validationRequirements"
            >
              <a-input
                v-model="entity.validationRequirements"
                placeholder="请输入验证要求"
                name="validationRequirements"
              ></a-input>
              </a-form-model-item>
            </a-col>
          <a-col :sm="24">
            <!-- 等级描述 -->
            <a-form-model-item 
              label="等级描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入描述"
                :rows="2"
                :max-length="500"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>
  
      <!-- 工艺参数 -->
      <g-collapse headerName="工艺参数">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-standard-btn" type="primary" icon="plus" @click="addStandard">添加标准</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="standardColumns" 
                :dataSource="entity.standards" 
                :pagination="false"
                size="small"
                rowKey="id"
                :scroll="{ x: '100%' }"
              >
                <template slot="standardName" slot-scope="text, record, index">
                  <a-input v-model="entity.standards[index].standardName" placeholder="请输入标准名称" />
                </template>
                <template slot="parameterType" slot-scope="text, record, index">
                  <a-select v-model="entity.standards[index].parameterType" placeholder="请选择参数类型">
                    <a-select-option value="数值型">数值型</a-select-option>
                    <a-select-option value="字符型">字符型</a-select-option>
                    <a-select-option value="布尔型">布尔型</a-select-option>
                    <a-select-option value="枚举型">枚举型</a-select-option>
                  </a-select>
                </template>
                <template slot="valueRange" slot-scope="text, record, index">
                  <a-input v-model="entity.standards[index].valueRange" placeholder="请输入参数值范围" />
                </template>
                <template slot="standardValue" slot-scope="text, record, index">
                  <a-input v-model="entity.standards[index].standardValue" placeholder="请输入标准值" />
                </template>
                <template slot="parameterUnit" slot-scope="text, record, index">
                  <a-input v-model="entity.standards[index].parameterUnit" placeholder="请输入参数单位" />
                </template>
                <template slot="tolerance" slot-scope="text, record, index">
                  <a-input v-model="entity.standards[index].tolerance" placeholder="请输入允差范围" />
                </template>
                <template slot="controlMethod" slot-scope="text, record, index">
                  <a-select v-model="entity.standards[index].controlMethod" placeholder="请选择控制方式">
                    <a-select-option value="SPC控制">SPC控制</a-select-option>
                    <a-select-option value="全检">全检</a-select-option>
                    <a-select-option value="抽检">抽检</a-select-option>
                    <a-select-option value="目视检查">目视检查</a-select-option>
                    <a-select-option value="其他">其他</a-select-option>
                  </a-select>
                </template>
                <template slot="inspectionMethod" slot-scope="text, record, index">
                  <a-input v-model="entity.standards[index].inspectionMethod" placeholder="请输入检验方法" />
                </template>
                <template slot="note" slot-scope="text, record, index">
                  <a-input v-model="entity.standards[index].note" placeholder="请输入说明" />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeStandard(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
  
      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditBatchLevel",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        parameterCardName: "",
        parameterCardType: "",
        description: "",
        standards: [],
        status: "启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
        registrationNo: "", // 医疗器械注册证号
        productionLicenseNo: "", // 生产许可证号
        riskLevel: "", // 风险等级
        validationRequirements: "", // 验证要求
      },
      rules: {
        parameterCardName: [
          { required: true, message: "请输入等级名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "等级名称不能包含前后空格", trigger: "change" }
        ],
        parameterCardType: [
          { required: true, message: "请选择参数卡类型", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" }
        ],
        registrationNo: [ // 医疗器械注册证号校验规则
          { required: false, message: "请输入医疗器械注册证号", trigger: "change" }
        ],
        productionLicenseNo: [ // 生产许可证号校验规则
          { required: false, message: "请输入生产许可证号", trigger: "change" }
        ],
        riskLevel: [ // 风险等级校验规则
          { required: false, message: "请选择风险等级", trigger: "change" }
        ],
        validationRequirements: [ // 验证要求校验规则
          { required: false, message: "请输入验证要求", trigger: "change" }
        ]
      },
      standardColumns: [
        {
          title: "标准名称",
          dataIndex: "standardName",
          key: "standardName",
          width: 100,
          scopedSlots: { customRender: "standardName" }
        },
        {
          title: "参数类型",
          dataIndex: "parameterType",
          key: "parameterType",
          width: 100,
          scopedSlots: { customRender: "parameterType" }
        },
        {
          title: "参数值范围",
          dataIndex: "valueRange",
          key: "valueRange",
          width: 100,
          scopedSlots: { customRender: "valueRange" }
        },
        {
          title: "标准值",
          dataIndex: "standardValue",
          key: "standardValue",
          width: 100,
          scopedSlots: { customRender: "standardValue" }
        },
        {
          title: "参数单位",
          dataIndex: "parameterUnit",
          key: "parameterUnit",
          width: 100,
          scopedSlots: { customRender: "parameterUnit" }
        },
        {
          title: "允差",
          dataIndex: "tolerance", // 新增允差字段
          key: "tolerance",
          width: 100,
          scopedSlots: { customRender: "tolerance" }
        },
        {
          title: "控制方式",
          dataIndex: "controlMethod", // 新增控制方式字段
          key: "controlMethod",
          width: 130,
          scopedSlots: { customRender: "controlMethod" }
        },
        {
          title: "检验方法",
          dataIndex: "inspectionMethod", // 新增检验方法字段
          key: "inspectionMethod",
          width: 130,
          scopedSlots: { customRender: "inspectionMethod" }
        },
        {
          title: "说明",
          dataIndex: "note",
          key: "note",
          width: 300,
          scopedSlots: { customRender: "note" }
        },
        {
          title: "操作",
          key: "action",
          width: 100,
          fixed: "right",
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取等级数据
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        parameterCardName: "",
        parameterCardType: "",
        description: "",
        standards: [],
        status: "启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      };
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        this.entity = {
          ...this.entity,
          parameterCardName: row.name, // 修改了这里
          levelType: row.levelType,
          description: row.description,
          standards: row.standards || [],
          status: row.status,
          createdBy: row.createdBy,
          createdTime: row.createdTime,
          modifiedBy: row.modifiedBy,
          modifiedTime: row.modifiedTime,
        };
      }
    },
    
    async save() {
      // 保存校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    // 添加等级标准
    addStandard() {
      this.entity.standards.push({
        id: Date.now(), // 使用时间戳作为临时ID
        standardName: "",
        standardValue: "",
        standardUnit: "",
        note: ""
      });
    },
    
    // 删除等级标准
    removeStandard(index) {
      this.entity.standards.splice(index, 1);
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.add-standard-btn {
  margin-bottom: 16px;
}
</style>
]]>
